import React, { useState } from 'react';
import { auth } from '../firebase/firebase';
import { updatePassword } from 'firebase/auth';

const CambiarPassword = () => {
  const [nuevaPassword, setNuevaPassword] = useState('');
  const [confirmarPassword, setConfirmarPassword] = useState('');
  const [guardandoPassword, setGuardandoPassword] = useState(false);
  const [mensaje, setMensaje] = useState('');
  const [error, setError] = useState('');

  const handleGuardarPassword = async () => {
    setMensaje('');
    setError('');

    if (!nuevaPassword.trim() || !confirmarPassword.trim()) {
      setError('Ambos campos son obligatorios.');
      return;
    }

    if (nuevaPassword.length < 8) {
      setError('La contrasea debe tener al menos 8 caracteres.');
      return;
    }

    if (nuevaPassword !== confirmarPassword) {
      setError('Las contraseas no coinciden.');
      return;
    }

    const user = auth.currentUser;
    if (!user) {
      setError('No hay un usuario autenticado.');
      return;
    }

    setGuardandoPassword(true);
    try {
      await updatePassword(user, nuevaPassword);
      setMensaje('Contrasea actualizada correctamente.');
      setNuevaPassword('');
      setConfirmarPassword('');
    } catch (error) {
      console.error('Error actualizando la contrasea:', error);
      // Algunos errores comunes: requiere reautenticacin, contrasea dbil, etc.
      if (error.code === 'auth/requires-recent-login') {
        setError('Por seguridad, vuelve a iniciar sesin y luego intenta cambiar la contrasea.');
      } else {
        setError(error.message);
      }
    } finally {
      setGuardandoPassword(false);
    }
  };

  return (
    <div className="w-50 mx-auto text-start">
      <h4 className="mt-4 mb-3">Cambiar Contrasea</h4>
      <input
        type="password"
        placeholder="Nueva contrasea"
        value={nuevaPassword}
        onChange={(e) => setNuevaPassword(e.target.value)}
        className="form-control mb-2"
      />
      <input
        type="password"
        placeholder="Confirmar contrasea"
        value={confirmarPassword}
        onChange={(e) => setConfirmarPassword(e.target.value)}
        className="form-control mb-3"
      />
      <button
        className="btn btn-primary w-100"
        onClick={handleGuardarPassword}
        disabled={guardandoPassword}
      >
        {guardandoPassword ? 'Guardando...' : 'Actualizar Contrasea'}
      </button>

      {mensaje && <p className="mt-3 text-success">{mensaje}</p>}
      {error && <p className="mt-3 text-danger">{error}</p>}
    </div>
  );
};

export default CambiarPassword;
