import React, { useEffect, useState } from 'react';
import { database } from '../firebase/firebase';
import { ref, onValue } from 'firebase/database';

const Principal = () => {
  const [categorias, setCategorias] = useState([]);

  useEffect(() => {
    const categoriasRef = ref(database, 'categorias');
    const unsubscribe = onValue(categoriasRef, (snapshot) => {
      const data = snapshot.val();
      const lista = data
        ? Object.entries(data).map(([id, cat]) => ({
            id,
            nombre: cat.nombre,
          }))
        : [];
      setCategorias(lista);
    });

    return () => unsubscribe();
  }, []);

  // Dividir categoras en dos columnas
  const mitad = Math.ceil(categorias.length / 2);
  const col1 = categorias.slice(0, mitad);
  const col2 = categorias.slice(mitad);

  return (
    <div className="text-center mt-4">
      <h2>Bienvenido a nuestra pgina de competencia.</h2>

      {/* {categorias.length > 0 ? (
        <div className="mt-3">
          <h5>Categoras disponibles:</h5>
          <div className="row justify-content-center">
            <div className="col-md-5">
              <ul className="list-group list-group-flush small">
                {col1.map((categoria) => (
                  <li
                    key={categoria.id}
                    className="list-group-item text-center"
                  >
                    {categoria.nombre}
                  </li>
                ))}
              </ul>
            </div>
            <div className="col-md-5">
              <ul className="list-group list-group-flush small">
                {col2.map((categoria) => (
                  <li
                    key={categoria.id}
                    className="list-group-item text-center"
                  >
                    {categoria.nombre}
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      ) : (
        <p className="text-muted">No hay categoras disponibles an.</p>
      )} */}
    </div>
  );
};

export default Principal;
