import React from 'react';
import CambiarPassword from './CambiarPassword';

const PerfilUsuario = ({ usuario, onLogout }) => {
  return (
    <section className="p-5 text-center bg-white">
      <h2>Perfil del Usuario</h2>
      <img
        src={usuario.fotoURL || 'https://via.placeholder.com/150'}
        alt="Foto de perfil"
        className="rounded-circle mb-3"
        width="150"
        height="150"
      />

      <p><strong>Nombre:</strong> {usuario.nombre}</p>
      <p><strong>Apellido:</strong> {usuario.apellido}</p>
      <p><strong>Email:</strong> {usuario.email}</p>
      <p><strong>Pas:</strong> {usuario.pais}</p>

      {/* <CambiarPassword usuario={usuario} /> */}

      <hr />
      {/* <button className="btn btn-danger mt-3" onClick={onLogout}>
        Cerrar sesin
      </button> */}
    </section>
  );
};

export default PerfilUsuario;
