// src/components/Sidebar.jsx
import React, { useState } from 'react';
import './Sidebar.css';

const Sidebar = ({ onSelect, usuario, admin, onLogout }) => {
  const [open, setOpen] = useState(false);

  const handleClick = (view) => {
    onSelect(view);
    setOpen(false);
  };

  return (
    <>
      <button className="btn btn-secondary m-3" onClick={() => setOpen(!open)}>
         Men
      </button>
      <div className={`sidebar ${open ? 'open' : ''}`}>
        <button className="close-btn" onClick={() => setOpen(false)}></button>

        {/* Mostrar Inicio siempre */}
        <a href="#" onClick={() => handleClick('inicio')}>Inicio</a>

        {/* Estas opciones solo aparecen si NO hay usuario ni admin */}
        {!usuario && !admin && (
          <>
            <a href="#" onClick={() => handleClick('registro')}>Registro</a>
            <a href="#" onClick={() => handleClick('acceder')}>Acceder</a>
          </>
        )}

        {/* Opciones para usuario logueado */}
        {usuario && (
          <>
            <a href="#" onClick={() => handleClick('perfil')}>Perfil</a>
            <a href="#" onClick={() => handleClick('editarPerfil')}>Editar usuario</a>
            <a href="#" onClick={() => handleClick('cambiarPassword')}>Cambiar contrasea</a>
            <a href="#" className="text-white" onClick={onLogout}>Cerrar sesin</a>
          </>
        )}

        {/* Opciones para administrador logueado */}
        {admin && (
          <>
            <a href="#" onClick={() => handleClick('admin')}>Panel Admin</a>
            <a href="#" onClick={() => handleClick('categorias')}>Categoras</a>
            <a href="#" className="text-white" onClick={onLogout}>Cerrar sesin</a>
          </>
        )}
      </div>
    </>
  );
};

export default Sidebar;
