import React, { useState } from 'react';
import Sidebar from './components/Sidebar';
import Footer from './components/Footer';
import RegistroForm from './components/RegistroForm';
import LoginForm from './components/LoginForm';
import PerfilUsuario from './components/PerfilUsuario';
import PerfilAdmin from './components/PerfilAdmin';
import Categorias from './components/Categorias'; // <-- Importa Categorias
import Header from './components/Header';
import Principal from './components/Principal';
import EditarPerfil from './components/EditarPerfil';
import CambiarPassword from './components/CambiarPassword';

const App = () => {
  const [view, setView] = useState('inicio');
  const [usuarioLogueado, setUsuarioLogueado] = useState(null);
  const [adminLogueado, setAdminLogueado] = useState(null);

  const handleLoginUsuario = (user) => {
    setUsuarioLogueado(user);
    setAdminLogueado(null);
    setView('perfil');
  };

  const handleLoginAdmin = (admin) => {
    setAdminLogueado(admin);
    setUsuarioLogueado(null);
    setView('admin');
  };

  const handleLogout = () => {
    setUsuarioLogueado(null);
    setAdminLogueado(null);
    setView('inicio');
  };

  const renderContent = () => {
    if (adminLogueado) {
      switch (view) {
        case 'admin':
          return <PerfilAdmin admin={adminLogueado} onLogout={handleLogout} />;
        case 'categorias':
          return <Categorias admin={adminLogueado} onLogout={handleLogout} />;
        default:
          return <PerfilAdmin admin={adminLogueado} onLogout={handleLogout} />;
      }
    }

    if (usuarioLogueado) {
      switch (view) {
        case 'perfil':
          return <PerfilUsuario usuario={usuarioLogueado} onLogout={handleLogout} />;
        case 'editarPerfil':
          return <EditarPerfil usuario={usuarioLogueado} />;
        case 'cambiarPassword':
          return <CambiarPassword usuario={usuarioLogueado} />;
        default:
          return <PerfilUsuario usuario={usuarioLogueado} onLogout={handleLogout} />;
      }
    }

    switch (view) {
      case 'registro':
        return <RegistroForm />;
      case 'acceder':
        return (
          <LoginForm
            onLogin={handleLoginUsuario}
            onLoginAdmin={handleLoginAdmin}
          />
        );
      default:
        return <Principal />;
    }
  };

  return (
    <>
      <Sidebar
        onSelect={setView}
        usuario={usuarioLogueado}
        admin={adminLogueado}
        onLogout={handleLogout}
      />
      <Header />
      {renderContent()}
      <Footer />
    </>
  );
};

export default App;
