import React, { useState } from 'react';
import { signInWithEmailAndPassword } from 'firebase/auth';
import { auth, database } from '../firebase/firebase';
import { ref, get } from 'firebase/database';

const LoginForm = ({ onLogin, onLoginAdmin }) => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');

  const handleLogin = async (e) => {
    e.preventDefault();
    setError('');

    try {
      const userCredential = await signInWithEmailAndPassword(auth, email, password);
      const user = userCredential.user;

      // Buscar si el usuario es admin
      const adminRef = ref(database, 'admin');
      const snapshot = await get(adminRef);

      if (snapshot.exists()) {
        const admins = snapshot.val();
        const adminId = Object.keys(admins).find(
          (key) => admins[key].email === user.email
        );

        if (adminId) {
          // Es admin
          onLoginAdmin({ uid: user.uid, email: user.email, ...admins[adminId] });
          return;
        }
      }

      // Si no es admin, buscar en usuarios
      const usuariosRef = ref(database, 'usuarios');
      const usuariosSnapshot = await get(usuariosRef);

      if (usuariosSnapshot.exists()) {
        const usuarios = usuariosSnapshot.val();
        const userId = Object.keys(usuarios).find(
          (key) => usuarios[key].email === user.email
        );

        if (userId) {
          onLogin({ uid: user.uid, email: user.email, ...usuarios[userId] });
        } else {
          setError('Usuario no encontrado en la base de datos.');
        }
      } else {
        setError('No hay usuarios registrados.');
      }

    } catch (err) {
      console.error('Error al iniciar sesin:', err);
      setError('Correo o contrasea incorrectos.');
    }
  };

  return (
    <form onSubmit={handleLogin} className="w-50 mx-auto mt-5">
      <h2>Iniciar Sesin</h2>
      <input
        type="email"
        placeholder="Correo electrnico"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        className="form-control mb-3"
        required
      />
      <input
        type="password"
        placeholder="Contrasea"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        className="form-control mb-3"
        required
      />
      <button className="btn btn-primary w-100" type="submit">
        Iniciar Sesin
      </button>
      {error && <p className="text-danger mt-3">{error}</p>}
    </form>
  );
};

export default LoginForm;
