import React, { useState, useEffect } from 'react';
import { database } from '../firebase/firebase';
import { ref, push, onValue, remove, update } from 'firebase/database';
import { Plus, Minus, ArrowUp, ArrowDown } from 'lucide-react';

const Categorias = () => {
  const [nuevaCategoria, setNuevaCategoria] = useState('');
  const [categorias, setCategorias] = useState([]);
  const [mensajeError, setMensajeError] = useState('');
  const [filtro, setFiltro] = useState(''); // <-- Nuevo estado para el filtro

  useEffect(() => {
    const categoriasRef = ref(database, 'categorias');
    const unsubscribe = onValue(categoriasRef, (snapshot) => {
      const data = snapshot.val();
      const lista = data
        ? Object.entries(data).map(([id, categoria]) => ({
            id,
            ...categoria,
          }))
        : [];
      setCategorias(lista);
    });

    return () => unsubscribe();
  }, []);

  const handleAgregarCategoria = () => {
    if (nuevaCategoria.trim() === '') return;

    const existe = categorias.some(
      (cat) => cat.nombre.toLowerCase() === nuevaCategoria.trim().toLowerCase()
    );

    if (existe) {
      setMensajeError('La categora ya existe. Intenta con otro nombre.');
      setTimeout(() => setMensajeError(''), 3000);
      return;
    }

    const categoriasRef = ref(database, 'categorias');
    const nuevoOrden = categorias.length + 1;

    push(categoriasRef, {
      nombre: nuevaCategoria.trim(),
      fechaCreacion: new Date().toISOString(),
      orden: nuevoOrden,
    })
      .then(() => setNuevaCategoria(''))
      .catch((error) => console.error('Error al agregar categora:', error));
  };

  const handleEliminarCategoria = (id) => {
    const categoriaRef = ref(database, `categorias/${id}`);
    remove(categoriaRef).catch((error) =>
      console.error('Error al eliminar categora:', error)
    );
  };

  const moverCategoria = (indiceActual, direccion) => {
    const nuevaLista = [...categorias].sort((a, b) => a.orden - b.orden);
    const nuevoIndice = indiceActual + direccion;

    if (nuevoIndice < 0 || nuevoIndice >= nuevaLista.length) return;

    const actual = nuevaLista[indiceActual];
    const otra = nuevaLista[nuevoIndice];

    const updates = {};
    updates[`categorias/${actual.id}/orden`] = otra.orden;
    updates[`categorias/${otra.id}/orden`] = actual.orden;

    update(ref(database), updates).catch((error) =>
      console.error('Error al mover categora:', error)
    );
  };

  // Filtro aplicado antes de mostrar
  const categoriasFiltradas = [...categorias]
    .sort((a, b) => a.orden - b.orden)
    .filter((cat) =>
      cat.nombre.toLowerCase().includes(filtro.trim().toLowerCase())
    );

  return (
    <div className="text-center mt-4">
      <h3>Categoras</h3>

      {mensajeError && (
        <div className="alert alert-warning w-50 mx-auto" role="alert">
          {mensajeError}
        </div>
      )}

      <div className="d-flex justify-content-center align-items-center gap-2 my-3">
        <input
          type="text"
          className="form-control w-50"
          placeholder="Nombre de la nueva categora"
          value={nuevaCategoria}
          onChange={(e) => setNuevaCategoria(e.target.value)}
        />
        <button className="btn btn-success" onClick={handleAgregarCategoria}>
          <Plus size={20} />
        </button>
      </div>

      {/* Filtro */}
      <div className="d-flex justify-content-center mb-3">
        <input
          type="text"
          className="form-control w-50"
          placeholder="Buscar por nombre"
          value={filtro}
          onChange={(e) => setFiltro(e.target.value)}
        />
      </div>

      {categoriasFiltradas.length > 0 ? (
        <table className="table table-striped table-bordered w-75 mx-auto">
          <thead className="table-dark">
            <tr>
              <th>Nombre</th>
              <th>Fecha de Creacin</th>
              <th>Orden</th>
              <th>Acciones</th>
            </tr>
          </thead>
          <tbody>
            {categoriasFiltradas.map((categoria, index, arr) => (
              <tr key={categoria.id}>
                <td>{categoria.nombre}</td>
                <td>{new Date(categoria.fechaCreacion).toLocaleString()}</td>
                <td>{categoria.orden}</td>
                <td className="d-flex justify-content-center gap-1">
                  <button
                    className="btn btn-outline-primary btn-sm"
                    onClick={() => moverCategoria(index, -1)}
                    disabled={index === 0}
                  >
                    <ArrowUp size={18} />
                  </button>
                  <button
                    className="btn btn-outline-primary btn-sm"
                    onClick={() => moverCategoria(index, 1)}
                    disabled={index === arr.length - 1}
                  >
                    <ArrowDown size={18} />
                  </button>
                  <button
                    className="btn btn-outline-danger btn-sm"
                    onClick={() => handleEliminarCategoria(categoria.id)}
                  >
                    <Minus size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      ) : (
        <p className="mt-4">No hay categoras registradas o no se encontraron coincidencias.</p>
      )}
    </div>
  );
};

export default Categorias;
