// src/components/EditarPerfil.jsx
import React, { useState, useEffect } from 'react';
import { ref as dbRef, get, update } from 'firebase/database';
import { ref as storageRef, uploadBytes, getDownloadURL } from 'firebase/storage';
import { database, storage } from '../firebase/firebase';

const EditarPerfil = ({ usuario }) => {
  const [formData, setFormData] = useState({
    nombre: usuario.nombre || '',
    apellido: usuario.apellido || '',
    pais: usuario.pais || '',
    foto: null,  // Foto nueva
    fotoURL: usuario.fotoURL || '',
  });

  const [guardandoDatos, setGuardandoDatos] = useState(false);
  const [mensaje, setMensaje] = useState('');
  const [error, setError] = useState('');
  const [paises, setPaises] = useState([]);

  useEffect(() => {
    // Traer lista de pases para el select
    fetch('https://restcountries.com/v3.1/all')
      .then((res) => res.json())
      .then((data) => {
        const nombres = data
          .map((pais) => pais.name?.common)
          .filter(Boolean)
          .sort((a, b) => a.localeCompare(b));
        setPaises(nombres);
      });
  }, []);

  const handleChange = (e) => {
    const { name, value, files } = e.target;
    if (name === 'foto') {
      setFormData({ ...formData, foto: files[0] });
    } else {
      setFormData({ ...formData, [name]: value });
    }
  };

  const handleGuardarDatos = async () => {
    setMensaje('');
    setError('');
    setGuardandoDatos(true);

    try {
      let fotoURL = formData.fotoURL;

      if (formData.foto) {
        // Subir nueva foto si se seleccion
        const fileRef = storageRef(storage, `fotos/${Date.now()}_${formData.foto.name}`);
        const snapshot = await uploadBytes(fileRef, formData.foto);
        fotoURL = await getDownloadURL(snapshot.ref);
      }

      // Obtener usuarios para buscar el id de este usuario
      const usuariosRef = dbRef(database, 'usuarios');
      const snapshot = await get(usuariosRef);

      if (snapshot.exists()) {
        const usuarios = snapshot.val();
        const idUsuario = Object.keys(usuarios).find(
          (key) => usuarios[key].email === usuario.email
        );

        if (idUsuario) {
          const usuarioRef = dbRef(database, `usuarios/${idUsuario}`);
          await update(usuarioRef, {
            nombre: formData.nombre,
            apellido: formData.apellido,
            pais: formData.pais,
            fotoURL,
          });
          setMensaje('Datos personales actualizados correctamente.');
          setFormData(prev => ({ ...prev, fotoURL })); // Actualizar fotoURL
        } else {
          setError('Usuario no encontrado.');
        }
      }
    } catch (error) {
      console.error('Error actualizando datos:', error);
      setError('Hubo un error al actualizar los datos.');
    } finally {
      setGuardandoDatos(false);
    }
  };

  return (
    <div className="w-50 mx-auto text-start">
      <h4 className="mb-3">Editar Perfil</h4>
      <label className="form-label">Nombre</label>
      <input
        type="text"
        name="nombre"
        value={formData.nombre}
        onChange={handleChange}
        className="form-control mb-2"
      />
      <label className="form-label">Apellido</label>
      <input
        type="text"
        name="apellido"
        value={formData.apellido}
        onChange={handleChange}
        className="form-control mb-2"
      />
      <label className="form-label">Pas</label>
      <select
        name="pais"
        value={formData.pais}
        onChange={handleChange}
        className="form-select mb-2"
      >
        <option value="">Seleccione un pas</option>
        {paises.map((pais) => (
          <option key={pais} value={pais}>
            {pais}
          </option>
        ))}
      </select>
      <label className="form-label">Foto de perfil</label>
      <input
        type="file"
        name="foto"
        accept="image/*"
        onChange={handleChange}
        className="form-control mb-2"
      />

      <button
        className="btn btn-success w-100 mb-4"
        onClick={handleGuardarDatos}
        disabled={guardandoDatos}
      >
        {guardandoDatos ? 'Guardando...' : 'Guardar Cambios'}
      </button>

      {mensaje && <p className="mt-3 text-success">{mensaje}</p>}
      {error && <p className="mt-3 text-danger">{error}</p>}
    </div>
  );
};

export default EditarPerfil;
